import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import Header from '../main/Header';
import Footer from '../main/Footer';
import Sidebar from '../main/Sidebar';
class GeneralLayout extends Component {
    constructor(props) {
        super(props);
        this.state = {
            
        }
    }
    componentDidMount() {
        document.body.className = 'no-skin';
    }

    render() {
        const { user } = this.props.auth;
        return (
            <div>
                <div id="navbar" className="navbar navbar-default ace-save-state">
                    <Header user={user} />
                </div>

                <div className="main-container ace-save-state" id="main-container">
                    <div id="sidebar" className="sidebar responsive ace-save-state">
                        <Sidebar />
                    </div>
                    <div className="main-content">
                        {this.props.children}
                    </div>

                    <div className="footer">
                        <Footer />
                    </div>

                    <a href="#" id="btn-scroll-up" className="btn-scroll-up btn btn-sm btn-inverse">
                        <i className="ace-icon fa fa-angle-double-up icon-only bigger-110"></i>
                    </a>
                </div>
            </div>
        )
    }
}

GeneralLayout.propTypes = {
    auth: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    auth: state.auth
});

export default connect(mapStateToProps)(GeneralLayout);