import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { loginUser } from '../../actions/authActions';
import LoginForm from '../form/LoginForm';
class Login extends Component {
    constructor() {
        super();
        this.state = {
            username: 'anivia',
            password: 'anivia',
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }
    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.auth.isAuthenticated) {
            this.props.history.push('/');
        }
        if (nextProps.errors) {
            this.setState({ errors: nextProps.errors });
            this.props.auth.loading = false;
        }
    }
    onSubmit(e) {
        e.preventDefault();

        const userData = {
            username: this.state.username,
            password: this.state.password
        }

        this.props.loginUser(userData);
    }
    componentDidMount() {
        document.body.className = 'login';
    }
    render() {
        const { errors } = this.state;
        const { loading } = this.props.auth;
        let loginLoading;
        if (loading) {
            loginLoading = <h1>Loading</h1>
        } else {
            loginLoading = (
                <button id="login" type="submit" className="width-35 pull-right btn btn-sm btn-primary">
                    <i className="ace-icon fa fa-key"></i>
                    <span className="bigger-110">Login</span>
                </button>
            )
        }
        return (
            <form onSubmit={this.onSubmit}>
                <fieldset>
                     <LoginForm
                        name="username"
                        placeholder="Tài khoản"
                        value={this.state.username}
                        onChange={this.onChange}
                        error={errors.username}
                        icon="ace-icon fa fa-user"
                    />

                    <LoginForm
                        name="password"
                        type="password"
                        placeholder="Mật khẩu tài khoản"
                        value={this.state.password}
                        onChange={this.onChange}
                        error={errors.password}
                        icon="ace-icon fa fa-lock"
                    />

                    <div className="space"></div>
                    <div className="clearfix">
                        {loginLoading}
                    </div>

                    <div className="space-4"></div>
                </fieldset>
            </form>
        )
    }
}
Login.propTypes = {
    loginUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired
}

const mapStateToProps = (state) => ({
    auth: state.auth,
    errors: state.errors
});

export default connect(mapStateToProps, { loginUser })(Login);
